import React, { useState } from 'react'
import { useNavigate } from 'react-router-dom'
import './login.scss'
import logo from '../../assets/logo/logo.png'
import useApi from '../../api';

const Login = () => {
  const [username, setUsername] = useState('')
  const [password, setPassword] = useState('')
  const [errorMessage, setErrorMessage] = useState('')

  console.log(process.env.REACT_APP_API_KEY)

  const navigate = useNavigate()
  const api = useApi();

  const handleSubmit = async (e) => {
    e.preventDefault()
    try {
      console.log(process.env.REACT_APP_API_KEY)
      const response = await api.post('/admin/login', { username, password }, {
        headers: {
          Authorization: process.env.REACT_APP_API_KEY
        }
      });
      localStorage.setItem('token', response.data.token)
      console.log(response.data.token)
      navigate('/app/home') // Navigate to the previous page or to the "home" page
    } catch (error) {
        if (error.response && error.response.status === 401) {
          setErrorMessage('Invalid credentials'); // Set the error message for 401 Unauthorized
        } else {
          setErrorMessage('An error has occurred'); // Set the generic error message for other errors
          console.error(error);
        }
    }
  }

  return (
    <div className="login">
      <div className="logo-container">
        <img src={logo} alt="Logo" className="logo" />
      </div>
      <form onSubmit={handleSubmit}>
        {errorMessage && <p>{errorMessage}</p>} {/* Display the error message */}
        <input type="text" className="input" id="username" name="username" value={username} onChange={e => setUsername(e.target.value)} placeholder="Username" required />
        <input type="password" className="input" id="password" name="password" value={password} onChange={e => setPassword(e.target.value)} placeholder="Password" required />
        <button type="submit" className="button">Login</button>
      </form>
    </div>
  )
}

export default Login
