import "./chart.scss"
import React from 'react';
import { AreaChart, Area, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer } from 'recharts';

const Chart = ({ monthlyIncidentCount }) => {
  const data = monthlyIncidentCount.map(item => ({
    ...item,
    month: item.month.trim(),
  }));

  return (
    <div className='chart'>
      <div className="title">Monthly Incident Count</div>
      <ResponsiveContainer width="100%" height={400}>
        <AreaChart
          data={data}
          margin={{
            top: 10,
            right: 30,
            left: 0,
            bottom: 0,
          }}
        >
          <CartesianGrid strokeDasharray="3 3" />
          <XAxis dataKey="month" />
          <YAxis />
          <Tooltip />
          <Area type="monotone" dataKey="incidentCount" stroke="#d82424" fill="#d82424" />
        </AreaChart>
      </ResponsiveContainer>
    </div>
  )
};

export default Chart;
