import React from 'react';
import "./featured.scss"
import { Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Paper } from '@mui/material';

export default function Featured({ incidentCountbyUser }) {
  return (
    <div className='featured'>
      <div className='left'>
        <span className="title">Users with Most Incidents</span>
      </div>
      <div className="right">
        <TableContainer component={Paper}>
          <Table>
            <TableHead>
              <TableRow align="left" >
                <TableCell className='subtitle'>Username</TableCell>
                <TableCell className='subtitle'>Incident Count</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {incidentCountbyUser.map((row) => (
                <TableRow key={row.username}>
                  <TableCell component="th" scope="row">
                    {row.username}
                  </TableCell>
                  <TableCell align="left">{row.incidentCount}</TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      </div>
    </div>
  )
};