import React from 'react'
import "./home.scss"
import Widget from "../../components/widgets/widget";
import Featured from "../../components/featured/featured";
import Chart from "../../components/chart/chart";
import useApi from '../../api';
import { useContext, useEffect, useState } from 'react';
import { TokenContext } from '../../context';

const Home = () => {

  const token = useContext(TokenContext);
  const api = useApi();
  const [homeData, setHomeData] = useState(null);

  const fetchData = async () => {
    try {
      const response = await api.get('/admin/home-page', {
        headers: {
          Authorization: process.env.REACT_APP_API_KEY,
          token
        }
      });
      setHomeData(response.data);
    } catch (error) {
      console.error(error);
    }
  };

  useEffect(() => {
    fetchData();
  }, []);

  if (!homeData) {
    return <div>Loading...</div>; // Display a loading message while the data is being fetched
  }

  return (
    <div>
      <div className='widgets'>
        <Widget type ="accident" count = {homeData.totalIncidentCount} />
        <Widget type ="user" count = {homeData.activeUserCount} />        
      </div>
      <div className="charts">
        <Featured incidentCountbyUser = {homeData.incidentCountbyUser} />
        <Chart monthlyIncidentCount = {homeData.monthlyIncidentCount} />
      </div>
    </div>
  )
}

export default Home
