import React from 'react'
import "./widget.scss"
import PeopleIcon from '@mui/icons-material/People';
import CarCrashIcon from '@mui/icons-material/CarCrash';

const widget = ({type, count}) => {

  let data;
  switch(type){
    case "user":
        data={
            title:"Total Users",
            counter: count,
            icon:<PeopleIcon className='icon' style={{backgroundColor:  "rgba(226, 39, 39, 0.274)"}}/>,

        };
        break;
        case "accident":
            data={
                title:"Total Incidents",
                counter: count,
                icon: <CarCrashIcon className='icon' style={{backgroundColor:  "rgba(226, 39, 39, 0.274)"}} />
    
        };
      break;           
    default:
      break;
  }

  return (
    <div className='widget'>
      <div className='left'>
        <span className="title">{data.title}</span>
        <span className="counter">{data.counter}</span>
        <span className="link"> {data.link}</span>
      </div>
      <div className="right">
        {data.icon}
      </div>
    </div>
  )
}

export default widget
