import React, { useState, useEffect } from 'react'
import { BrowserRouter as Router, Routes, Route, Navigate, useLocation, useNavigate } from 'react-router-dom'
import Sidebar from "./components/sidebar/sidebar";
import Navbar from "./components/navbar/navbar";
import Login from './pages/login/login'
import Home from './pages/home/home'
import UserList from './pages/user-list/user-list'
import Accident from './pages/accident info/accident'
import Spinner from './components/spinner/spinner'
import NotFound from './components/not-found/not-found.jsx'
import { TokenContext } from './context.js';

const basePath = process.env.REACT_APP_BASE_PATH

const RedirectTo404 = () => {
  const navigate = useNavigate();

  useEffect(() => {
    navigate('/404');
  }, [navigate]);

  return null;
};

function App() {
  return (
    <Router basename={basePath}>
      <Routes>
        <Route path="/login" element={<Login />} />
        <Route path="/app/*" element={<ProtectedRoute><MainLayout /></ProtectedRoute>} />
        <Route path="/" element={<ProtectedRoute><MainLayout /></ProtectedRoute>} />
        <Route path="/404" element={<NotFound />} /> {/* 404 page */}
        <Route path="*" element={<NotFound />} /> {/* Catch-all route */}
      </Routes>
    </Router>
  );
}

const MainLayout = () => (

  <div className='home'>
    <Sidebar/>
    <div className="homeContainer"> 
      <Navbar/>
      <Routes>
        <Route path="/" element={<ProtectedRoute><Navigate to="home" replace /></ProtectedRoute>} />
        <Route path="home" element={<ProtectedRoute><Home /></ProtectedRoute>} />
        <Route path="accident" element={<ProtectedRoute><Accident /></ProtectedRoute>} />
        <Route path="user-list" element={<ProtectedRoute><UserList /></ProtectedRoute>} />
        <Route path="*" element={<RedirectTo404 />} /> {/* Catch-all route */}
        {/* Add more protected routes as needed */}
      </Routes>
    </div>
  </div>
);

const ProtectedRoute = ({ children }) => {
  const [isChecking, setIsChecking] = useState(true);
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [token, setToken] = useState(null);
  const location = useLocation();

  useEffect(() => {
    const storedToken = localStorage.getItem('token');
    console.log(storedToken)
    setIsAuthenticated(!!storedToken);
    setToken(storedToken);
    setIsChecking(false);
  }, []);

  if (isChecking) {
    return <Spinner />; // Display the spinner while checking
  }

  return isAuthenticated ? (
    <TokenContext.Provider value={token}>
      {children}
    </TokenContext.Provider>
  ) : <Navigate to="/login" state={{ from: location }} replace />;
};

export default App;